import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';  //  ethers.js
import SimpleGridABI from './SimpleGridABI.json';  //  ABI  
import './App.css';

const App = () => {
  const [provider, setProvider] = useState(null);
  const [contract, setContract] = useState(null);
  const [grid, setGrid] = useState([]);
  const [loading, setLoading] = useState(false);
  const contractAddress = "0x56f6bB37cB1Cb27d653e436DCCdE222cE4656FdA";  //     

  //     Metamask
  const connectWallet = async () => {
    if (window.ethereum) {
      const prov = new ethers.providers.Web3Provider(window.ethereum);  // Web3 
      await prov.send('eth_requestAccounts', []);  //    
      const signer = prov.getSigner();  //  signer    
      const contractInstance = new ethers.Contract(contractAddress, SimpleGridABI, signer);  //  
      setProvider(prov);  //  
      setContract(contractInstance);  //  
    } else {
      alert(" Metamask!");
    }
  };

  //     
  const fetchGrid = async () => {
    setLoading(true);  //   
    const tempGrid = [];
    if (contract) {
      for (let x = 0; x < 3; x++) {
        for (let y = 0; y < 3; y++) {
          const cell = await contract.getCell(x, y);  //   
          tempGrid.push({ x, y, content: cell });
        }
      }
      setGrid(tempGrid);  //     
    }
    setLoading(false);  //  
  };

  //    ""
  const placeOre = async (x, y) => {
    if (contract) {
      try {
        await contract.placeOre(x, y);  //  ""  
        fetchGrid();  //    
      } catch (error) {
        console.error(" :", error);
      }
    }
  };

  return (
    <div className="App">
      <h1>Simple Grid DApp</h1>
      {!provider ? (
        <button onClick={connectWallet}> </button>
      ) : (
        <div>
          <button onClick={fetchGrid} disabled={loading}>
            {loading ? "..." : " "}
          </button>
          <div>
            {grid.map((cell, index) => (
              <div key={index}>
                <p> ({cell.x}, {cell.y}): {cell.content}</p>
                {cell.content === "Empty" && (
                  <button onClick={() => placeOre(cell.x, cell.y)}>
                     
                  </button>
                )}
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default App;
